
.disend{
    /* padding: 15px 0px; */
    width: fit-content;
    display: flex;
}

.disendfirst{
    min-width: 200px;
    width: fit-content;
    border-right: 1px solid black;
}

.disendfirstul{
    list-style: none;
    margin-top: 30px;
}

.disendli{
    margin: 10px;
}

.disendli:hover{
    background-color: rgb(222, 220, 220);
    width: fit-content;
    border-radius: 5px;
}

.disendlibtn{
    border: none;
    background: none;
    cursor: pointer;
    font-size: 18px;
}


.disendsecond{
    width: fit-content;
    overflow: auto;
    flex-wrap: wrap;
    padding: 30px;
}

.details{
    display: none;
}
.location1{
    display: none;
}
.working{
    display: none;
}
.contact{
    display: none;
}
.weak{
    display: none;
}

.selected{
    display: block;
}