.hlist{
    border: 1px solid white;
    width: 90vw;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    overflow: auto;
    margin: 20px;
    border: 2px solid black;
}

.dis{
    padding: 0px;
    width: 100%;
}

.disheading{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 25px;
    flex-wrap: wrap;
    margin-top: 10px;
    margin-left: 20px;
}

