.disphoto{
    width: 150px;
    height: auto;
    width: 20%;
    padding: 10px;
    margin-right: 30px;
    z-index: 2;
    border-radius: 15px;
}

.disphoto img{
    width: 100%;
    z-index: 100;
    object-fit: contain;
    border-radius: 15px;
    border: 1px solid black;
}

.disli{
    list-style: none;
}

.location{
    list-style: none;
    font-size: 20px;
    margin-top: 10px;
    color: rgba(46, 46, 46, 0.775);
}
